import React from 'react';
import {Route, Switch, withRouter} from 'react-router-dom'

//pages
// import Home from './pages/Home'

//krasny-yar
import KY from './pages/KY/Home'

import KY_About from './pages/KY/About'
import KY_Cakes from './pages/KY/Cakes'
import KY_Feedback from './pages/KY/Feedback'
import KY_Loyalty from './pages/KY/Loyalty'
import KY_Production from './pages/KY/Production'
import KY_Stocks from './pages/KY/Stocks'
import KY_Team from './pages/KY/Team'
import KY_ToSuppliers from './pages/KY/ToSuppliers'


import Ryadom from './pages/Ryadom/Home'
import Baton from './pages/Baton/Home'

const routes = [
  {
    path: '/krasny-yar/about',
    component: KY_About
  },
  {
    path: '/krasny-yar/cakes',
    component: KY_Cakes
  },
  {
    path: '/krasny-yar/feedback',
    component: KY_Feedback
  },
  {
    path: '/krasny-yar/loyalty-program',
    component: KY_Loyalty
  },
  {
    path: '/krasny-yar/production',
    component: KY_Production
  },
  {
    path: '/krasny-yar/stocks',
    component: KY_Stocks
  },
  {
    path: '/krasny-yar/command',
    component: KY_Team
  },
  {
    path: '/krasny-yar/partners',
    component: KY_ToSuppliers
  },
  {
    path: '/krasny-yar',
    component: KY
  },
  {
    path: '/ryadom',
    component: Ryadom
  },
  {
    path: '/baton',
    component: Baton
  },
  {
    path: '/',
    component: KY
  },
  {
    path: '*',
    component: KY
  }
]

const App = () => {
  const router = 
    <Switch >
      { routes.map((route, index) => 
        <Route  
          key={index}
          path={route.path} 
          component={route.component}
        />)
      }
    </Switch>
  return router
}

export default withRouter(App);
