import React, { useState } from 'react';
import './style.scss'
import Container from '@material-ui/core/Container';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';

import CustomCake from './components/CustomCake'
import ReadyCakes from './components/ReadyCakes'

const components = [
  <ReadyCakes />,
  <CustomCake />
]

export default () => {
  const [currentComponent, setComponent] = useState(1)

  const handleChange = (event, index) => {
    setComponent(index)
  }

  return (
    <Container>
      <div className="CakeForm__wrapper">
        <Tabs
          style={{width: '200px'}}
          orientation="vertical"
          variant="scrollable"
          value={currentComponent}
          onChange={handleChange}
          aria-label="Vertical tabs example"
          // className={classes.tabs}
        >
          <Tab label="Готовые торты"/>
          <Tab label="Сделай сам"/>
         
        </Tabs>
        <div className="CakeForm__form">
          { components[currentComponent] }
        </div>
      </div>
    </Container>
  )
}