import React, { Component } from "react";
import './style.scss'

import ArrowBackIos from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIos from '@material-ui/icons/ArrowForwardIos';
import Carousel from '@brainhubeu/react-carousel';
import '@brainhubeu/react-carousel/lib/style.css';
import IconButton from '@material-ui/core/IconButton';

import image1 from '../../assets/images/сайт.jpg'
import image2 from '../../assets/images/Rich_Colon_banner.jpg'

const classes = {
  image: {
    width: '100%'
  },
  wrapper: {
    position: 'relative',
    zIndex: 1,
  },
  arrowRight: {
    position: 'absolute',
    top: '50%',
    right: '10px',
    zIndex: 2,
  },
  arrowLeft: {
    position: 'absolute',
    top: '50%',
    left: '10px',
    zIndex: 2,
  },

}

export default class SimpleSlider extends Component {
  render() {
    return (
      <div style={{...classes.wrapper}} className="Slider" >
        <Carousel
          dots
          infinite
          arrowLeft={<IconButton color="secondary" style={{...classes.arrowLeft}}><ArrowBackIos /></IconButton>}
          arrowRight={<IconButton color="secondary" style={{...classes.arrowRight}}><ArrowForwardIos /></IconButton>}
          addArrowClickHandler
          autoPlay={5000}
          animationSpeed={1000}
        >
          <img style={{...classes.image}} className="Slider__item" alt="item" src={image1} />
          <img style={{...classes.image}} className="Slider__item" alt="item" src={image2} />
        </Carousel>
      </div>
    )  
  }
}