export default function DisabledTabs() {
  const [itemIndex, setIndex] = React.useState(0);

  const handleChange = (event, newValue) => {
    setIndex(newValue);
  };

  return (
    <>
      <Container>
        <h1>Адресса гастрономов</h1>
      </Container>
      <AppBar position="false" color="secondary">
        <Container>
          <Tabs
            variant="scrollable"
            scrollButtons="auto"
            value={itemIndex}
            indicatorColor="primary"
            onChange={handleChange}
            aria-label="disabled tabs example"
          >
            { items.map((item, index) => <Tab key={index} label={item.title} />) }
          </Tabs>
        </Container>
      </AppBar>
      <Container style={{display: 'flex', flexWrap: 'wrap' }}>
      { items[itemIndex] && items[itemIndex].items && items[itemIndex].items.map((item, index) => <CityAdresses key={index} item={item}/>) }
      </Container>
    </>
  );
}