import React, { useState } from 'react';
import CakeIcon from '@material-ui/icons/Cake';
import IconButton from '@material-ui/core/IconButton';
import { NavLink  } from 'react-router-dom';

import './style.scss'

export default () => {
  const [isHover, setIsHover] = useState(false);
  
  const onHoverHandler = is => setIsHover(is)
  const btnCls = ['ToCakeButton__button']
  if (!isHover) {
    btnCls.push('ToCakeButton__button--animation')
  }
  return (
    <NavLink to="/krasny-yar/cakes">
      <div className="ToCakeButton__wrapper">
        { isHover && <span onMouseLeave={onHoverHandler.bind(this, false)} onMouseEnter={onHoverHandler.bind(this, true)} className="ToCakeButton__text">Заказать торт</span> }
        <div color="secondary" className={btnCls.join(' ')}>
          <IconButton 
            color="secondary"
            onMouseEnter={onHoverHandler.bind(this, true)} 
            onMouseLeave={onHoverHandler.bind(this, false)} 
            aria-label="delete"
          >
            <CakeIcon fontSize="large" />
          </IconButton>

        </div>
      </div>
    </NavLink>
  )
}